import Vue from 'vue'
import VueRouter from 'vue-router'
import Store from '@/store'
Vue.use(VueRouter)

// 路由懒加载使app页面小一点，加载快一点
// 把组件对应js，分成若干.js路由切换到哪个页面，然后对应加载js文件
// 原因: webpack分析入口时, 发现router里上来就import所有页面, 所以直接打包进app.js->很大
// 解决: 当路由路径匹配规则时, 才现去import引入对应的组件js文件
// webpackChunName注释语法, 给生成的js文件起名字

const routes = [
  { path: '/login', name: 'login', component: () => import('@/views/login') },
  { path: '/search', name: 'search', component: () => import('@/views/search') },
  {
    path: '/search_result/:value',
    name: 'searchResult',
    component: () => import('@/views/search/searchResult'),
    props($route) {
      return { ...$route.params }
    }
  },
  {
    path: '/article_detail',
    name: 'artDetail',
    component: () => import('@/views/articleDetail'),
    props($route) {
      return { ...$route.query }
    }
  },
  {
    path: '/',
    name: 'layout', // 父路由有默认子路由，它的name没有意义
    component: () => import('@/views/layout'),
    redirect: '/home',
    children: [
      {
        // path: '', // 默认子路由,只能有一个
        path: '/home',
        name: 'home',
        component: () => import('@/views/home')
      },
      {
        path: '/qa',
        name: 'qa',
        component: () => import('@/views/qa')
      },
      {
        path: '/video',
        name: 'video',
        component: () => import('@/views/video')
      },
      {
        path: '/my',
        name: 'my',
        component: () => import('@/views/my')
      }
    ]
  },
  // 用户编辑
  { path: '/useredit', name: 'useredit', component: () => import('@/views/my/UserEdit') },
  { path: '/chat', name: 'chat', component: () => import('@/views/chat') }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  // 用户已登录且有token，不允许跳转登录页
  // to and from are both route objects. must call `next`.
  if (Store.state.user && to.path === '/login') {
    next(false)
  } else {
    next()
  }
})

export default router
